@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Alice&family=Pacifico&family=Roboto&family=Roboto+Slab&family=Satisfy&display=swap');

$backgroundColorGold: rgba(192, 163, 110, 0.96);
$borderBlue: #00576e 2px solid;

@mixin buttonBasic {
    max-width: 150px;
    width: 150px;
    height: 50px;
    border: 1px solid #00576e;
    color: white;
    background-color: #00576e;
    letter-spacing: 0.1rem;
}


html,
body {
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Alice', 'Arial', sans-serif;
}

body {
    background-image: url(./assets/homePicture3.jpg);
    background-size: cover;
}

// SECTION LOGO
header {
    text-align: center;

    img {
        height: 8rem;
        margin-bottom: 1rem;
        border-radius: 80%;
    }
}

//SECTION NAV
nav {
    padding-bottom: 0.8rem;
    background-color: rgba(98, 96, 96, 0.447);
    display: flex;
    flex-wrap: wrap;

    button,
    .btn {
        flex-basis: 150px;
        flex-grow: 1;
        background: transparent;
        border: 0;
        border-radius: 0;
        padding: 1rem 0 0.2rem 0;
        display: inline-block;
        position: relative;
        color: #f8f8f8;
        font-size: 1.2rem;
        letter-spacing: 0.2rem;

        div {
            padding: 0 0;
            margin: 0 0;
            display: inline-block;
            position: relative;

            &:after {
                content: '';
                position: absolute;
                width: 110%;
                transform: scaleX(0);
                height: 2px;
                bottom: -5px;
                left: 0;
                right: 0;
                margin: 0 auto;
                background-color: #F2C879;
                transform-origin: bottom right;
                transition: transform 0.25s ease-out;
            }

            &:hover:after {
                transform: scaleX(1);
                transform-origin: bottom left;
            }

            &:hover {
                color: #f8f8f8;
            }
        }

        @media (max-width: 992px) {
            flex-basis: 235px;

        }

        @media (max-width: 768px) {
            flex-basis: 200px;

        }

        @media (max-width: 576px) {
            flex-basis: 180px;

        }
    }
}

//SEKCION MAIN - BASIC SETTINGS
main {

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin: 2rem 0 0 0;
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p {
        font-family: Alice;
    }

    ul {
        list-style: none;
        padding: 0;
        display: flex;
        justify-items: center;
        justify-content: space-around;
        flex-wrap: wrap;

        li {
            min-height: 330px;
            min-width: 300px;
            display: flex;
            flex-direction: column;
            flex-basis: 100px;
            margin: 2rem;
            justify-content: space-around;
            color: black; // padding: 1rem;
            border: $borderBlue;
            background-color: rgba(192, 163, 110, 0.914);

            h5 {
                font-family: Pacifico, sans-serif;
            }

            p,
            h5,
            h6 {
                margin: 0.4rem;
                text-align: center;
                justify-content: space-around;
            }

            footer {
                display: flex;
                align-items: center;
                justify-content: space-around;
                padding: 0.4rem;

                &>*+* {
                    margin-left: 1rem;
                }
            }

            button,
            .btn {
                background: transparent;
                border: 1px solid #00576e;
                color: white;
                background-color: #00576e;
                letter-spacing: 0.1rem;
                font-size: 0.9rem;

                &:hover {
                    color: rgba(192, 163, 110);
                }
            }
        }

        li+li {
            margin-top: 2rem;
        }
    }

}


//SECTION CART NAV 
.cartNavSection {
    display: none;
    flex-direction: column;
    padding: 1rem;
    padding-bottom: 0;
    border: 1px solid #00576e;
    background-color: rgba(192, 163, 110, 0.914);
    position: absolute;
    top: 40%;
    right: 2%;


    th,
    td {
        text-align: center;
    }

    h2 {
        margin: 0.2rem;
    }
}



//SECTION - BASIC SETTINGS
section {
    h2 {
        text-shadow: #00576e 1px 1px 1px;
    }

    h2,
    p {
        text-align: center;
        color: #f8f8f8;
    }

}

.datesBooking {
    color: black;
    background-color: #F2C879;
}


//SECTION TREATMENTS

.treatments {
    border-top: #00576e 1px solid;
    margin-top: 2rem;

    ul {
        margin: 0;
    }

    li {
        min-height: 350px;
        min-width: 270px;
        background-color: #f2c8799f;
    }

    h2 {
        margin: 0;
        padding-top: 2rem;
        color: black;
        text-align: center;
        text-shadow: none;
    }

    h5 {
        margin-bottom: 1rem;
    }

    h6 {
        font-size: 1.1rem;
        font-style: italic;
        font-family: Alice, 'Arial', sans-serif;
        padding: 0;
        text-align: center;
    }

    p {
        margin-top: 1rem;
        color: #f8f8f8;
    }

    background-color: #F2C879;
    background-color: $backgroundColorGold;

}

.rooms {
    margin-top: 2rem;

    ul {
        margin-bottom: 0;
    }

    h2 {
        text-shadow: #00576e 1px 1px 1px;
    }

    h2,
    p {
        text-align: center;
        color: #f8f8f8;
    }

    @media (max-width: 1500px) {
        border-top: #00576e 1px solid;
        margin-top: 2rem;
        background-color: $backgroundColorGold;

        li {
            background-color: #f2c8799f;
        }

        h2 {
            margin-top: 0;
            padding-top: 2rem;
            color: black;
            text-shadow: none;
        }
    }
}


//SECION ROOM DETAILS
.roomDetails {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 2rem 0 4rem 0;

    p {
        display: flex;
        flex-direction: column;
        max-width: 400px;
        background-color: #f2c879ff;
        border: $borderBlue;

        div {
            padding: 1rem;
        }
    }

    button {

        margin-left: 2rem;
        background: transparent;
        @include buttonBasic();
        font-size: 0.9rem;
        padding: 0.6rem 1rem;

        &:hover {
            color: rgba(192, 163, 110);
        }
    }
}

//SEKCJA CART
.cart {

    display: flex;
    flex-direction: column;
    margin: 2rem;
    padding-bottom: 2rem;
    background-color: $backgroundColorGold;
    border: #00576e 1px solid;

    td,
    th {
        max-width: 50px;
        padding-left: 2rem;
        text-align: center;
    }

    h2,
    p {
        text-align: center;
    }

    th {
        text-decoration: underline;
    }

    td button {
        background-color: rgba(32, 31, 31, 0.616);
        padding: 0.2rem;
        margin: 0.4rem;
    }
}


//SEKCJA HOME STYLE
.home {
    display: flex;
    flex-direction: column;

    .reservation {
        display: flex;
        flex-direction: column;
        margin-bottom: 4rem;

        .divWrapperDates {
            margin-top: 3rem;
            display: flex;
            justify-content: center;
            align-items: center;

            .wrapperLabel1,
            .wrapperLabel2 {
                display: flex;
                flex-direction: column;
            }

            label {
                border-radius: 100% 100% 0% 0%;
                font-family: Alice, serif;
                text-align: center;
                margin-left: 1rem;
                color: white;
                color: rgba(192, 163, 110);
                font-size: 1.4rem;
                background-color: rgba(50, 49, 49, 0.907);
            }

            input {
                border-radius: 5%;
                text-align: center;
                margin-left: 1rem;
                width: 160px;
                height: 60px;
                font-size: 1.1rem;
            }
        }

        button {
            align-self: center;
            margin-top: 1.8rem;
            margin-left: 2rem;
            background: transparent;
            @include buttonBasic();
            font-size: 1.15rem;

            &:hover {
                background-color: rgba(192, 163, 110);
                color: #00576e;
                font-weight: 600;
            }
        }
    }

    h2 {
        margin-top: 4rem;
    }

    p {
        font-style: italic;
    }

    h2,
    p {
        text-align: center;
        color: #f8f8f8;
        text-shadow: #00576e 2px 2px 2px;
    }
}


@media (max-width: 576px) {
    nav {
        display: flex;
        flex-direction: column;
        justify-content: center;

        button {
            flex-basis: 60px;

        }

    }
}

.footer {
    background-color: rgb(192, 163, 110);
    background-color: #f2c879;
    padding: 0;
    margin-top: 0; // background-color: #F2C879;
    border-top: $borderBlue;
    position: fixed;
    bottom: 0;
    width: 100%;
}


//SECTION BOOKING
.booking {
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    .bookingDetails {
        flex-direction: row;
        max-width: 700px;
        flex-wrap: wrap;
        color: white;
        display: flex;
        background-color: $backgroundColorGold;
        padding: 2rem 1rem;
        border: $borderBlue;

        form {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
        }

        .sectionInputs,
        div {
            min-width: 300px;
            display: flex;
            flex-direction: column;
            margin-bottom: 0.7rem;
            padding: 0 2rem;
            // justify-content: center;
            align-content: space-around;
            justify-content: flex-end;

            @media (max-width: 576px) {
                min-width: 300px;
            }

            input {
                flex-direction: row;
                min-width: 250px;
                padding: 0.5rem 0.3rem;
            }
        }

        .divWrapperDates {
            align-content: space-around;
            align-self: flex-start;
            margin-left: -2.5rem;

            @media (max-width: 576px) {
                margin-left: 0;
            }
        }

        .datesInputs {
            input {
                width: 199.4px;

                @media (max-width: 576px) {
                    min-width: 270px;
                }
            }
        }

        button {
            margin: 1.5rem;
            align-self: center;
            @include buttonBasic();

            &:hover {
                color: rgba(192, 163, 110);
            }
        }
    }

    h2 {
        margin-top: 2rem;
    }

    p {
        font-style: italic;
    }

    h2,
    p {
        text-align: center;
        color: #f8f8f8;
        text-shadow: #00576e 2px 2px 2px;
    }
}



//SECTION LOG IN
.login {
    margin: 0rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    h2,
    p {
        text-align: center;
        color: #f8f8f8;
        text-shadow: #00576e 2px 2px 2px;
    }

    .loginDetails {
        flex-direction: column;
        min-width: 350px;
        min-height: 250px;
        flex-wrap: wrap;
        color: white;
        display: flex;
        margin-top: 1rem;
        background-color: $backgroundColorGold;
        padding: 2rem 1rem;
        border: $borderBlue;

        form {
            flex-direction: column;
            display: flex;
            justify-content: space-around;

            .sectionLogin,
            .sectionPassword {
                display: flex;
                flex-direction: column;
                align-items: center;
                padding-bottom: 1rem;

                input {
                    min-width: 250px;
                    padding: 0.5rem 0.3rem;
                }

                label {
                    font-family: Alice, serif;
                    text-align: center;
                    color: white;
                    font-size: 1.2rem;
                }
            }

            button {
                max-width: 150px;
                margin-top: 1rem;
                align-self: center;
                width: 150px;
                height: 50px;
                border: 1px solid #00576e;
                color: white;
                background-color: #00576e;
                letter-spacing: 0.1rem;

                &:hover {
                    color: rgba(192, 163, 110);
                }
            }
        }
    }

    .sectionSignin {
        display: flex;
        flex-direction: row;
        color: white;
        background-color: $backgroundColorGold;
        min-width: 350px;
        justify-content: space-between;
        align-items: center;
        border: 2px solid #00576e;

        p {
            margin: 0;
            padding-left: 1rem;
        }

        .signInButton {
            @include buttonBasic();

            &:hover {
                color: rgba(192, 163, 110);
            }
        }
    }
}



// SECTION REGISTRATION
.registration {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    color: white;
    display: flex;
    align-content: center;

    h2 {
        margin-bottom: 1rem;
        text-align: center;
        color: #f8f8f8;
        text-shadow: #00576e 2px 2px 2px;
    }

    form {
        display: flex;
        flex-direction: column;
        padding: 2rem;
        background-color: $backgroundColorGold;
    }

    .sectionLogin,
    .sectionPassword,
    .sectionPassword2 {
        display: flex;
        flex-direction: column;

        input {
            min-width: 250px;
            padding: 0.5rem 0.3rem;
        }

        label {
            padding-top: 0.5rem;
            font-family: Alice, serif;
            text-align: center;
            color: white;
            font-size: 1.1rem;
            align-self: flex-start;
        }
    }

    button {
        margin-top: 1.5rem;
        align-self: center;
        @include buttonBasic();

        &:hover {
            color: rgba(192, 163, 110);
        }
    }
}



//SECTION USER
.userSection {
    flex-basis: 250px;
    margin: 4rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    div {
        display: flex;
        flex-direction: column;
        padding: 2rem;
        background-color: $backgroundColorGold;
    }


    h2 {
        margin: 0;
        background-color: $backgroundColorGold;
        align-self: center;
    }

    button {
        align-self: center;
        margin-top: 1.5rem;
        align-self: center;
        @include buttonBasic();
        margin: 1rem 1rem;

        &:hover {
            color: rgba(192, 163, 110);
        }

    }
}